
.cart-control {
  font-size: 0;
}
.cart-control .cart-decrease {
  display: inline-block;
  padding: 6px;
  transition: all 0.4s linear;
}
.cart-control .cart-decrease .inner {
  display: inline-block;
  color: #00a0dc;
  font-size: 24px;
  line-height: 24px;
}
.cart-control .cart-decrease.move-enter-active,
.cart-control .cart-decrease.move-leave {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}
.cart-control .cart-decrease.move-enter-active .inner,
.cart-control .cart-decrease.move-leave .inner {
  transition: all 0.4s linear;
  transform: rotate(0);
}
.cart-control .cart-decrease.move-leave-active .inner {
  transition: all 0.4s linear;
}
.cart-control .cart-decrease.move-enter,
.cart-control .cart-decrease.move-leave-active {
  opacity: 0;
  transform: translate3d(24px, 0, 0);
}
.cart-control .cart-decrease.move-enter .inner,
.cart-control .cart-decrease.move-leave-active .inner {
  transform: rotate(180deg);
}
.cart-control .cart-count {
  display: inline-block;
  font-size: 10px;
  color: #93999f;
  line-height: 36px;
  vertical-align: top;
  width: 12px;
  text-align: center;
}
.cart-control .cart-add {
  display: inline-block;
  padding: 6px;
  color: #00a0dc;
  font-size: 24px;
  line-height: 24px;
}


