
.food {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 48px;
  z-index: 30;
  width: 100%;
  background: #fff;
}
.food.move-enter-active,
.food.move-leave {
  transform: translate3d(0, 0, 0);
}
.food.move-leave-active,
.food.move-enter-active {
  transition: all 0.4s linear;
}
.food.move-enter,
.food.move-leave-active {
  transform: translate3d(100%, 0, 0);
}
.food .food-content .image-header {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.food .food-content .image-header img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.food .food-content .image-header .back {
  position: absolute;
  top: 10px;
  left: 0;
}
.food .food-content .image-header .back .icon-arrow_lift {
  display: block;
  padding: 10px;
  font-size: 20px;
  color: #fff;
}
.food .food-content .content {
  position: relative;
  padding: 18px;
}
.food .food-content .content .title {
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
  line-height: 14px;
}
.food .food-content .content .sell {
  margin-top: 8px;
  font-size: 10px;
  color: #93999f;
  line-height: 10px;
}
.food .food-content .content .sell .rating {
  margin-left: 12px;
}
.food .food-content .content .price {
  margin-top: 18px;
  font-weight: 700;
  line-height: 24px;
}
.food .food-content .content .price .now {
  font-size: 14px;
  color: #f01414;
}
.food .food-content .content .price .old {
  text-decoration: line-through;
  font-size: 10px;
  color: #93999f;
  margin-left: 12px;
}
.food .food-content .content .cart_control-wrapper {
  position: absolute;
  bottom: 12px;
  right: 12px;
}
.food .food-content .content .buy {
  position: absolute;
  bottom: 18px;
  right: 18px;
  width: 74px;
  height: 24px;
  line-height: 24px;
  text-align: center;
  font-size: 10px;
  color: #fff;
  border-radius: 12px;
  background-color: #00a0dc;
  z-index: 10;
}
.food .food-content .content .buy.fade-enter-active,
.food .food-content .content .buy.fade-leave {
  opacity: 1;
}
.food .food-content .content .buy.fade-leave-active,
.food .food-content .content .buy.fade-enter-active {
  transition: all 0.2s;
}
.food .food-content .content .buy.fade-enter,
.food .food-content .content .buy.fade-leave-active {
  opacity: 0;
}
.food .food-content .info {
  padding: 18px;
}
.food .food-content .info .title {
  font-size: 14px;
  color: #07111b;
  line-height: 14px;
}
.food .food-content .info .text {
  font-size: 12px;
  font-weight: 200;
  color: #4d555d;
  line-height: 24px;
  margin: 6px 8px 0px;
}
.food .food-content .rating {
  padding-top: 18px;
}
.food .food-content .rating .title {
  font-size: 14px;
  color: #07111b;
  line-height: 14px;
  margin-left: 18px;
}
.food .food-content .rating .rating-wrapper {
  padding: 0 18px;
}
.food .food-content .rating .rating-wrapper .rating-item {
  position: relative;
  padding: 16px 0;
  position: relative;
}
.food .food-content .rating .rating-wrapper .rating-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.food .food-content .rating .rating-wrapper .rating-item .user {
  position: absolute;
  right: 0;
  top: 16px;
  font-size: 0;
  line-height: 12px;
  font-size: 0;
}
.food .food-content .rating .rating-wrapper .rating-item .user .name {
  display: inline-block;
  vertical-align: top;
  font-size: 10px;
  color: #93999f;
  line-height: 12px;
  margin-right: 6px;
}
.food .food-content .rating .rating-wrapper .rating-item .user .avatar {
  display: inline-block;
  vertical-align: top;
  border-radius: 50%;
}
.food .food-content .rating .rating-wrapper .rating-item .text {
  font-size: 0;
}
.food .food-content .rating .rating-wrapper .rating-item .text .txt {
  display: inline-block;
  vertical-align: top;
  line-height: 16px;
  color: #07111b;
  font-size: 12px;
}
.food .food-content .rating .rating-wrapper .rating-item .text .icon {
  display: inline-block;
  vertical-align: top;
  line-height: 16px;
  margin-right: 4px;
  font-size: 12px;
  color: #93999f;
}
.food .food-content .rating .rating-wrapper .rating-item .text .icon.icon-thumb_down {
  color: #93999f;
}
.food .food-content .rating .rating-wrapper .rating-item .text .icon.icon-thumb_up {
  color: #00a0dc;
}
.food .food-content .rating .rating-wrapper .rating-item .time {
  font-size: 10px;
  color: #93999f;
  line-height: 12px;
  margin-bottom: 6px;
}


